.Layout {
  height: 100%;
  box-sizing: border-box;
  display: grid;
  grid-template-rows: 10% auto 5%;
  grid-template-columns: repeat(8, 1fr);
}

.Layout > header,
.Layout > footer {
  grid-column: span 8;
}

.Layout > main {
  grid-column: span 8;
}

.Layout,
.Layout > * {
  border: 1px solid lightgrey;
}
